.container {
  /* width: 100vw; */
  overflow: hidden;
  margin: 0 0 0 0;
  padding: 1rem 0 1rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.container h2 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.container ul {
  height: 15rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  overflow: hidden;
  flex-wrap: wrap;
  background-color: transparent;
  /* border-top: 1px solid black; */
  /* border-bottom: 1px solid black; */
}

.left {
  position: absolute;
  left: 0;
  height: 2rem;
  width: 2rem;
  z-index: 2;
  margin-top: 2rem;
  font-size: 1.2rem;
}

.right {
  position: absolute;
  right: 0;
  height: 2rem;
  width: 2rem;
  z-index: 2;
  margin-top: 2rem;
  font-size: 1.2rem;
}
